
import { mapState, mapGetters, mapMutations } from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Card from 'vux/src/components/card/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Popup from 'vux/src/components/popup/index.vue'


export default {
components: {
XHeader,
Tabbar,
TabbarItem,
Card,
TransferDom,
Group,
Cell,
XButton,
Drawer,
ViewBox,
Radio,
Loading,
Calendar,
Selector,
PopupPicker,
Picker,
XInput,
Flexbox,
FlexboxItem,
Popup
},
directives: {
TransferDom
},
watch: {
},
mounted () {

this.$nextTick(function () {
if (this.$route.path == '/orderList' || this.$route.path == '/waitOrderList') {
setTimeout(() => {
this.box = document.querySelector('#vux_view_box_body')
if (this.box) {
this.box.removeEventListener('scroll', this.handler, false)
this.box.addEventListener('scroll', this.handler, false)
}
}, 1000)
}
})

this.handler = () => {
if (this.$route.path === '/orderList' || this.$route.path == '/waitOrderList') {
this.box = document.querySelector('#vux_view_box_body')
let scrollTop = this.box.scrollTop
this.$store.commit('updatePagePosition', {top: scrollTop})
}
}
},
data () {
return {
show: true,
drawerVisibility: false,
showMode: 'overlay',
showModeValue: 'overlay',
showPlacement: 'right',
showPlacementValue: 'right',
starDate: '2017-07-13',
endDate: '2017-07-13',
keyword: '',
selectState: [''],
orderState: [['', '', '']],
show7: true,
path: this.$route.path
}
},
computed: {
...mapState({
orders: 'orders',
clickOrderIndex: 'clickOrderIndex'
}),
...mapGetters({
waitOrders: 'waitOrders',
waitOrdersNum: 'waitOrdersNum',
currentOrder: 'currentOrder'
}),
headTitle () {
if (this.$route.params.id) {
return this.currentOrder.number
} else {
return this.$route.name
}
},
viewOrderUrl () {
if (this.clickOrderIndex !== '') {
return '/orderview/' + this.currentOrder.id
}
},
ManOrderUrl () {
if (this.clickOrderIndex !== '') {
return '/orderManage/' + this.currentOrder.id
}
},
leftOptions () {
return {
showBack: this.$route.path !== '/'
}
},
rightOptions () {
return {
showMore: false
}
}
},
created () {
this.$store.dispatch('getData')
},
methods: {
clickTab () {
console.log('in')
},
onClickMore () {
this.showMenu = true
},
onShowModeChange (val) {
/** hide drawer before changing showMode **/
this.drawerVisibility = false
setTimeout(one => {
this.showModeValue = val
}, 400)
},
onPlacementChange (val) {
/** hide drawer before changing position **/
this.drawerVisibility = false
setTimeout(one => {
this.showPlacementValue = val
}, 400)
}
}
}



